//  -    
const getMyPostAX = () => {
  return function (dispatch, getState, { history }) {
    if (token) {
      axiosModule
        .get('/posts/myPosts')
        .then((res) => {
          logger('   res', res);
          let posts = [];

          if (res.data.length !== 0) {
            res.data.forEach((p) => {
              let hour = p.orderTime.split(' ')[1].split(':')[0];
              let minute = p.orderTime.split(' ')[1].split(':')[1];

              const my_post = {
                post_id: p.postId,
                title: p.title,
                contents: p.contents,
                category: p.category,
                shop: p.restaurant,
                headCount: p.headCount,
                orderTime: hour + ':' + minute,
                orderDate: p.orderTime.split(' ')[0],
                address: p.address.split('/')[0],
                detail_address: p.address.split('/')[1],
                insert_dt: p.createdAt,
                username: p.username,
                user_id: p.userId,
                userImg: p.profileImg,
                // distance: p.distance,
                room_id: p.roomId,
                nowHeadCount: p.nowHeadCount,
                valid: p.valid,
              };
              posts.push(my_post);
            });
          }
          dispatch(setMyPost(posts));
        })
        .catch((e) => {
          logger('   1111111', e);
        });
    } else {
      dispatch(logOut());
    }
  };
};

//  -    
const getMyReviewAX = () => {
  return function (dispatch, getState, { history }) {
    if (token) {
      axiosModule
        .get('/review')
        .then((res) => {
          logger('   res', res);
          let reviews = [];

          if (res.data.length !== 0) {
            res.data.forEach((p) => {
              const my_review = {
                user_profile: p.profileImg,
                user_nickname: p.username,
                my_manner: p.manner,
                review: p.review,
                insert_dt: p.createdAt,
              };
              reviews.push(my_review);
            });
          }
          dispatch(setMyReview(reviews));
        })
        .catch((e) => {
          logger('   ', e);
        });
    } else {
      dispatch(logOut());
    }
  };
};

//    -  
const reviewWriteAX = (manner, review, user_id, nickname) => {
  return function (dispatch, getState, { history }) {
    if (token) {
      axiosModule
        .post(`/review/${user_id}`, {
          mannerType: manner,
          review: review,
        })
        .then((res) => {
          logger('   res', res);
          customAlert.sweetReviewWrite(
            '  !',
            `${nickname} `,
            '   :)',
            'goBack'
          );
          // history.replace("/userprofile");
          // window.location.replace("/userprofile");
        })
        .catch((e) => {
          logger('   ', e);
          customAlert.sweetConfirmReload(
            '  !',
            '  .',
            'goBack'
          );
        });
    } else {
      dispatch(logOut());
    }
  };
};
// Reducer
export default handleActions(
  {
    [SET_USER]: (state, action) =>
      produce(state, (draft) => {
        draft.user = action.payload.user_info;
        draft.is_login = true;
        draft.is_loaded = true;
        logger('set_user ', draft.user);
      }),
    [SET_ANOTHER_USER]: (state, action) =>
      produce(state, (draft) => {
        draft.anotherUser = action.payload.user_info;
        logger('set_another_user ', draft.anotherUser);
      }),
    [SET_MYREVIEW]: (state, action) =>
      produce(state, (draft) => {
        draft.myReview.push(...action.payload.my_review);
        logger('set_my_review ', draft.myReview);
      }),
    [SET_MYPOST]: (state, action) =>
      produce(state, (draft) => {
        draft.myPost.push(...action.payload.my_post);
        logger('set_my_post ', draft.myPost);
      }),
    [LOG_OUT]: (state, action) =>
      produce(state, (draft) => {
        sessionStorage.removeItem('token');
        draft.user = null;
        draft.is_login = false;
        draft.is_loading = false;

        customAlert.sweetConfirmReload(
          ' .',
          ' !',
          '/'
        );
      }),
    [LOADING]: (state, action) =>
      produce(state, (draft) => {
        draft.is_loading = action.payload.is_loading;
      }),
    [EDIT_PROFILE]: (state, action) =>
      produce(state, (draft) => {
        draft.user.user_nickname = action.payload.profile.username;
        draft.user.user_comment = action.payload.profile.comment;
        draft.user.user_profile = action.payload.profile.profileImg;
        draft.user.user_age = action.payload.profile.user_age;
        draft.user.user_gender = action.payload.profile.user_gender;

      }),
    [EDIT_ADDRESS]: (state, action) =>
      produce(state, (draft) => {
        draft.user.user_address = action.payload.address;
      }),
  },
  initialState
);

const actionCreators = {
  kakaoLogin,
  loginCheck,
  logOut,
  loading,
  editUserProfileAX,
  editUserAddressAX,
  findUserProfileAX,
  getMyReviewAX,
  getMyPostAX,
  reviewWriteAX,
};

export { actionCreators };
